.home {
    position: absolute;
    min-height: 100vh;
    width: 100%;
    z-index: 100;
    top: 0;

    background-color: #000;
    display: flex;
    flex-direction: column;
    justify-content: start;
    align-items: center;
}

.home-nav {
    box-sizing: border-box;
    padding: 15px 25px;
    display: flex;
    flex-direction: row;
    justify-content: center;
}

.home-nav a {
    margin: 0 20px;
    color: #fff;
    font-size: 20px;
}

.cards {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
}

.logo-box {
    position: relative;
    margin: 0;
    user-select: none;
    margin-top: 50px;
}

.home .title {
    color: #fff;
    font-size: 100px;
    font-family: EB Garamond,serif;
}

.home .logo-block {
    top: 10%;
    height: 80%;
    width: 180px;
    background-color: #bf642f;
    border-radius: 3px;
    position: absolute;
    z-index: -1;
}

.home .home-loading {
    color: #fff;
    font-size: 48px;
}

@media only screen and (max-width: 800px){
    .logo-block {
        display: none;
    }
    .home .title {
        font-size: 40px;
    }
}